<template>
    <div class="duel">
        <header>
            <img id='logo' src="./../assets/logo.svg">
            <img id='menu' src="./../assets/menu.svg">
            <hr>
        </header>
        <article>
            <div  @click='calculer' class="calcul">{{ calcul }}</div>
        </article>
        <div class="board">
            <div class="touche" v-for="n in 10" @click="afficher(n - 1)">
                {{ n - 1 }}
            </div>
            <div class="touche" @click="moins">
                -
            </div>
            <div class="touche" @click="supprimer">
                C
            </div>
        </div>
        <article>
            <div class="resultat">{{ inserer }}</div>
            <img @click='valider'id='play' src="./../assets/play.svg">
        </article>
    </div>

</template>

<script>
export default {
    name: 'Duel',
    data: function() {
        return {
            inserer: '',
            calcul: null,
            calculA: null,
            calculB: null,
            operateur: ['+', '-', '*']
        }
    },
    methods: {
        afficher: function(v){
            this.inserer += v ;
        },
        supprimer: function(){
            const a = this.inserer.substring(0, this.inserer.length-1);
            this.inserer = a;
        },
        moins: function(){
            if(this.inserer === ''){
                this.inserer = '-';
            }
        },
        valider: function(){
            alert('ok');
        },
        calculer: function(){
            this.calculA = Math.floor(Math.random() * (20 - 0)) + 0;
            this.calculB = Math.floor(Math.random() * (20 - 0)) + 0;
            const temp = Math.floor(Math.random() * (3 - 0)) + 0;
            this.calcul = this.calculA + this.operateur[temp]+this.calculB;

        }

    },
}
</script>

<style scoped>
#menu{
    position: absolute;
    width: 60px;
    height: 60px;
    right: 20px;
    top: 22px;
}
#logo{
    width: 100px;
    height: 100px;
    position: absolute;
    top: 0;
}
.board {
    display: grid;
    grid-template-columns: repeat(4, 60px);
    grid-template-rows: repeat(4, 60px);
    position: absolute;
    top: 280px;
    left: 50px;

}
.touche {
    color: #fff;
    border: 3px solid #E41D1A;
    background: #44A5A5;
    border-radius: 10px;
    font-size: 2em;
    margin: 1px;
    text-align: center;
}


hr{
    height: 10px;
    background: #44A5A5;
    color: #44A5A5;
    position: relative;
    top: 80px;
}
.calcul{
    width: 70vw;
    height: 100px;
    background: black;
    border-radius: 20px;
    position: absolute;
    top: 150px;
    left: 50px;
    color: #fff;
    font-size: 2em;
    text-align: center;
    line-height: 100px;
}

.resultat{
    width: 70vw;
    height: 100px;
    background: lightblue;
    border-radius: 20px;
    position: absolute;
    top: 500px;
    left: 50px;
    text-align: center;
    font-size: 2em;
    color: #fff;
    line-height: 100px;
}
#play{
    width: 80px;
    height: 80px;
    position: absolute;
    top: 510px;
    right: 20px;
}
</style>



